@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;1,300;1,400&display=swap);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body{font-family:'Open Sans', sans-serif;margin:0;padding:0;color:#333}h1,h2{color:#2A0C4E}.container{max-width:1000px;margin:0 auto;padding:0 15px}.flex{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.action-buttons{margin-left:auto}.action-buttons .fa{margin-right:10px;font-size:24px;margin-top:0px}.book-holder{height:55px;border:1px solid;margin:20px 0;padding:20px;font-size:18px;border-radius:4px;margin-left:3rem;display:block;text-decoration:none;color:#333}.book-holder .fa{font-size:36px;margin-right:1rem;color:#51355A}.book-holder .book-name{font-weight:700;text-transform:capitalize}.group-holder{margin-bottom:3rem}.group-holder .group-header{position:-webkit-sticky;position:sticky;top:100px;background:royalblue;color:#fff;padding:1rem;float:left}header{position:-webkit-sticky;position:sticky;background:#fff;top:0;width:100%;z-index:10;box-shadow:0 4px 15px rgba(0,0,0,0.1);margin-bottom:1rem}.book-detail-holder .back{margin-right:10px}.book-detail-holder .fa{font-size:36px;margin-right:1rem}.book-detail-holder .book-name{font-size:24px}.book-detail-holder .detail-holder{width:70%;margin:3rem auto;padding:3rem;box-shadow:0 0 25px rgba(0,0,0,0.15);background-color:#FFF8F0}.book-detail-holder .description{margin-top:2rem;margin-left:3rem}.book-detail-holder .count{margin-left:3rem}.book-detail-holder strong+p{margin-top:5px}.book-detail-holder .title-author{width:100%}.book-detail-holder input,.book-detail-holder textarea{border:0;width:100%;font-family:"Open Sans";pointer-events:none;font-size:18px;box-sizing:border-box;background-color:transparent;transition:margin ease .4s, border ease .2s, padding .4s ease}.book-detail-holder .author-input{width:250px}.book-detail-holder.edit input,.book-detail-holder.edit textarea,.book-detail-holder.add input,.book-detail-holder.add textarea{pointer-events:all;border:2px solid #2A0C4E;margin:10px;padding:5px}.action-holder{margin-left:auto}.btn{padding:7px 30px;text-transform:uppercase;cursor:pointer;text-align:center;min-width:75px;text-decoration:none;margin-left:10px}.btn.btn-outline{border:1px solid #2A0C4E}.btn.btn-outline:hover{color:#fff;background-color:#2A0C4E}.btn.btn-primary{color:#fff;background-color:#2A0C4E}.search{font-size:24px;padding:5px 15px}@media (max-width: 600px){h1{font-size:24px}h2{font-size:18px}.search{font-size:16px}.btn{padding:7px 10px;font-size:14px}.group-holder .group-header{top:60px;float:none;font-size:18px;font-weight:700;padding:0.7rem 1rem}.book-holder{min-height:45px;padding:10px;height:auto;font-size:16px;margin-left:0}.book-detail-holder .detail-holder{width:auto;margin:2rem auto;padding:2rem;box-shadow:0 0 25px #00000026;background-color:#FFF8F0}.flex{-webkit-flex-wrap:wrap;flex-wrap:wrap;width:100%}.book-detail-holder .description,.book-detail-holder .count{margin-left:0;margin-bottom:1rem}.action-buttons{margin-bottom:1rem}.list-holder>.flex:first-child{box-shadow:0 8px 10px rgba(0,0,0,0.1);position:-webkit-sticky;position:sticky;top:113px;background:#fff;padding:0.1rem 0.5rem;width:100%;box-sizing:border-box;z-index:10;border-top:1px solid gray}.group-holder{padding:0 0.7rem}}@media (max-width: 500px){.action-buttons,.action-holder{margin-left:0}.search{width:74%}}

